html,body,p,ol,ul,li,dl,dt,dd,blockquote,figure,fieldset,legend,textarea,pre,iframe,hr,h1,h2,h3,h4,h5,h6{margin:0;padding:0;font-family:"Open Sans",sans-serif;font-size:13px}h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:normal}ul{list-style:none}button,input,select,textarea{margin:0}html{box-sizing:border-box}*{box-sizing:inherit}*:before,*:after{box-sizing:inherit}img,embed,iframe,object,audio,video{height:auto;max-width:100%}iframe{border:0}table{border-collapse:collapse;border-spacing:0}td,th{padding:0;text-align:left}pre,#console{overflow-x:auto;white-space:pre-wrap;background:#000;color:#fff;font-family:Consolas, 'Andale Mono WT', 'Andale Mono', 'Lucida Console', 'Lucida Sans Typewriter', 'DejaVu Sans Mono', 'Bitstream Vera Sans Mono', 'Liberation Mono', 'Nimbus Mono L', Monaco, 'Courier New', Courier, monospace}*{box-sizing:border-box}html{background-color:#434343;background-image:linear-gradient(#434343, #282828)}body{background-color:transparent;background-image:linear-gradient(0deg, transparent 24%, rgba(255,255,255,0.05) 25%, rgba(255,255,255,0.05) 26%, transparent 27%, transparent 74%, rgba(255,255,255,0.05) 75%, rgba(255,255,255,0.05) 76%, transparent 77%, transparent),linear-gradient(90deg, transparent 24%, rgba(255,255,255,0.05) 25%, rgba(255,255,255,0.05) 26%, transparent 27%, transparent 74%, rgba(255,255,255,0.05) 75%, rgba(255,255,255,0.05) 76%, transparent 77%, transparent);background-size:50px 50px}#root{display:inline-block;width:100%}#console{width:314px;height:153px;position:fixed;top:20px;right:20px;opacity:0.75;max-width:100%;z-index:4}#console ul,#console ol,#console li{list-style:none}#console li{margin:10px}#console li:before{content:'$: '}.playground{background:#fff url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAAECAYAAACp8Z5+AAAAIklEQVQYV2NkQAIPHjz4zwjjgzgKCgqMYAEYB8RmROaABAAIDA+kyr+jbwAAAABJRU5ErkJggg==) center center repeat;width:60vw;margin:36px auto;padding:19px;border:1px solid #e4e1e1;box-shadow:0 0 0 2px white inset;border-radius:3px}.playground__percentage{position:sticky;top:19px;left:0;width:100%;background:#f1f1f1;padding:15px;display:block;border-radius:5px;border:1px solid #dedede;box-shadow:0 0 0 1px #f9f9f9 inset;color:#444;opacity:0;transition:opacity 0.4s ease}.playground__percentage.visible{opacity:1}.playground__percentage [data-percentage]{font-style:normal}.playground__percentage [data-percentage]::before{content:attr(data-percentage)}.playground__percentage [data-percentage]::after{content:'%'}.playground__area{min-height:100vh;width:1024px;max-width:100%;margin:0 auto;display:flex;flex-direction:row}.playground__area__inner{width:100%;min-height:100vh;align-items:center;display:flex;justify-content:center;flex-direction:column}.playground__area__row{display:block;width:auto;font-size:0;padding-left:0;padding-right:0;min-width:100%;margin-left:-2px;margin-right:-2px}.playground__col{width:25%;display:inline-block;vertical-align:top;padding-left:2px;padding-right:2px}.playground__col--full{width:100%;display:inline-block;vertical-align:top;padding-left:2px;padding-right:2px}.playground__item{background:#fdfdfd;border-radius:4px;box-shadow:0 4px 11px -5px rgba(66,66,66,0.5);border:1px solid #fff;padding:4px;opacity:0;transition:opacity 0.4s ease}.playground__item img,.playground__item video{display:block}.playground__col--full .playground__item img,.playground__col--full .playground__item video{width:100%}.playground__item.loaded{opacity:1}
